import { Component, Prop, Event, EventEmitter, h, Watch, Element } from '@stencil/core';
import {
  Grid,
  ModuleRegistry,
  GridOptions,
  ColDef,
  ICellRendererParams,
  ILoadingOverlayComp,
  INoRowsOverlayComp,
  createGrid,
} from '@ag-grid-community/core';
import { ClientSideRowModelModule } from '@ag-grid-community/client-side-row-model';

import '@ag-grid-community/styles/ag-grid.css';
import '@ag-grid-community/styles/ag-theme-alpine.css';

ModuleRegistry.registerModules([ClientSideRowModelModule]);

export interface GridColumn extends ColDef {
  header: string;
  field: string;
  width?: number;
  sortable?: boolean;
  filterable?: boolean | string;
  renderCell?: (params: ICellRendererParams) => any;
}

class AgLoadingOverlayComponent implements ILoadingOverlayComp {
  private eGui!: HTMLElement;
  init(): void {
    this.eGui = document.createElement('div');
    this.eGui.className = 'ag-custom-overlay ag-custom-loading-overlay';
    this.eGui.innerHTML = 'Loading data...';
  }
  getGui(): HTMLElement {
    return this.eGui;
  }
}

class AgNoRowsOverlayComponent implements INoRowsOverlayComp {
  private eGui!: HTMLElement;
  private params: any;
  init(params: any): void {
    this.params = params;
    this.eGui = document.createElement('div');
    this.eGui.className = 'ag-custom-overlay ag-custom-no-rows-overlay';
    this.eGui.innerHTML = params.message || 'No data available.';
  }
  getGui(): HTMLElement {
    return this.eGui;
  }
}

@Component({
  tag: 'custom-grid',
  styleUrl: 'custom-grid.css',
})
export class CustomGrid {
  @Element() hostElement!: HTMLElement;

  @Prop() data: any[] = [];
  @Prop() columns: GridColumn[] = [];
  @Prop() rowHeight: number = 40;
  @Prop() headerHeight: number = 40;
  @Prop() striped: boolean = false;
  @Prop() hoverEffect: boolean = true;
  @Prop() resizableColumns: boolean = false;
  @Prop() sortable: boolean = false;
  @Prop() filterable: boolean = false;
  @Prop() pagination: boolean = false;
  @Prop() pageSize: number = 10;
  @Prop({ mutable: true }) currentPage: number = 1;
  @Prop() loading: boolean = false;
  @Prop() emptyMessage: string = 'No data available.';
  @Prop() columnDefaults: Partial<ColDef> = {
    sortable: true,
    filter: 'agTextColumnFilter',
  };

  @Event() gridSortChange!: EventEmitter<any>;
  @Event() gridFilterChange!: EventEmitter<any>;
  @Event() gridPageChange!: EventEmitter<number>;
  @Event() gridRowClick!: EventEmitter<any>;

  private gridApi: any;
  private gridDiv!: HTMLDivElement;

  private getFilterType(filterableProp: boolean | string | undefined, globalFilterable: boolean): ColDef['filter'] {
    if (filterableProp === false) return false;
    if (typeof filterableProp === 'string') return filterableProp;
    return (filterableProp || globalFilterable) ? 'agTextColumnFilter' : false;
  }

  @Watch('loading')
  onLoadingChange(newValue: boolean) {
    if (this.gridApi) {
      newValue ? this.gridApi.showLoadingOverlay() : this.gridApi.hideOverlay();
    }
  }

  @Watch('data')
  onDataChange(newData: any[]) {
    if (this.gridApi) {
      this.gridApi.setRowData(newData);
      if (!newData || newData.length === 0) this.gridApi.showNoRowsOverlay();
      else this.gridApi.hideOverlay();
    }
  }

  @Watch('columns')
  onColumnsChange(newColumns: GridColumn[]) {
    if (this.gridApi) {
      const columnDefs: ColDef[] = newColumns.map(col => {
        const agColDef: ColDef = {
          headerName: col.header,
          field: col.field,
          width: col.width,
          resizable: col.resizable,
          flex: col.flex,
          minWidth: col.minWidth,
          maxWidth: col.maxWidth,
          filter: this.getFilterType(col.filterable, this.filterable),
          sortable: col.sortable === false ? false : (col.sortable || this.sortable),
        };
        if (col.renderCell) {
          agColDef.cellRenderer = (params: ICellRendererParams) => {
            const result = col.renderCell!(params);
            if (result instanceof HTMLElement) return result;
            if (result && typeof result.outerHTML === 'string') return result.outerHTML;
            return String(result);
          };
        }
        return agColDef;
      });
      this.gridApi.setColumnDefs(columnDefs);
    }
  }

  private onGridReady = (params: any) => {
    this.gridApi = params.api;
    if (this.loading) this.gridApi.showLoadingOverlay();
    else if (!this.data || this.data.length === 0) this.gridApi.showNoRowsOverlay();

    if (this.pagination && this.currentPage > 1) {
      this.gridApi.paginationGoToPage(this.currentPage - 1);
    }
  };

  private onSortChanged = (params: any) => {
    this.gridSortChange.emit(params.api.getSortModel());
  };

  private onFilterChanged = (params: any) => {
    this.gridFilterChange.emit(params.api.getFilterModel());
  };

  private onPaginationChanged = (params: any) => {
    const newPage = params.api.paginationGetCurrentPage() + 1;
    if (newPage !== this.currentPage) {
      this.currentPage = newPage;
      this.gridPageChange.emit(this.currentPage);
    }
  };

  private onRowClicked = (params: any) => {
    this.gridRowClick.emit(params.data);
  };

  componentDidLoad() {
    setTimeout(() => this.initializeGrid(), 0);
  }

  disconnectedCallback() {
    if (this.gridApi) {
      this.gridApi.destroy();
      this.gridApi = null;
    }
  }

  private initializeGrid() {
    const columnDefs: ColDef[] = this.columns.map(col => {
      const agColDef: ColDef = {
        headerName: col.header,
        field: col.field,
        width: col.width,
        resizable: col.resizable,
        flex: col.flex,
        minWidth: col.minWidth,
        maxWidth: col.maxWidth,
        filter: this.getFilterType(col.filterable, this.filterable),
        sortable: col.sortable === false ? false : (col.sortable || this.sortable),
      };
      if (col.renderCell) {
        agColDef.cellRenderer = (params: ICellRendererParams) => {
          const result = col.renderCell!(params);
          if (result instanceof HTMLElement) return result;
          if (result && typeof result.outerHTML === 'string') return result.outerHTML;
          return String(result);
        };
      }
      return agColDef;
    });

    const cleanedColumnDefaults: Partial<ColDef> = { ...this.columnDefaults };
    // @ts-ignore
    if ('filterable' in cleanedColumnDefaults) {
      cleanedColumnDefaults.filter = this.getFilterType(cleanedColumnDefaults.filterable as boolean | string, this.filterable);
      delete cleanedColumnDefaults.filterable;
    }

    const defaultColDef: ColDef = {
      flex: 1,
      minWidth: 100,
      resizable: this.resizableColumns,
      sortable: this.sortable,
      filter: this.getFilterType(undefined, this.filterable),
      ...cleanedColumnDefaults,
    };

    const standardPageSizes = [10, 20, 50, 100];
    const paginationPageSizeOptions = this.pagination
      ? Array.from(new Set([...standardPageSizes, this.pageSize])).sort((a, b) => a - b)
      : false;

    const gridOptions: GridOptions = {
      rowData: this.data,
      columnDefs,
      rowHeight: this.rowHeight,
      headerHeight: this.headerHeight,
      pagination: this.pagination,
      paginationPageSize: this.pageSize,
      paginationPageSizeSelector: paginationPageSizeOptions,
      defaultColDef,
      domLayout: 'normal',
      loadingOverlayComponent: AgLoadingOverlayComponent,
      noRowsOverlayComponent: AgNoRowsOverlayComponent,
      noRowsOverlayComponentParams: { message: this.emptyMessage },
      onGridReady: this.onGridReady,
      onSortChanged: this.onSortChanged,
      onFilterChanged: this.onFilterChanged,
      onPaginationChanged: this.onPaginationChanged,
      onRowClicked: this.onRowClicked,
    };

    if (this.gridDiv && (this.gridDiv.offsetHeight > 0 || this.gridDiv.offsetWidth > 0)) {
      this.gridApi = createGrid(this.gridDiv, gridOptions);
    } else {
      console.warn('AG Grid container has zero dimensions. Grid might not render.');
    }
  }

  render() {
    return (
      <div
        class={{
          'ag-grid-wrapper': true,
          'ag-theme-alpine': true,
          'ag-grid-no-striped': !this.striped,
          'ag-grid-no-hover': !this.hoverEffect,
        }}
        style={{ height: '100%', width: '100%' }}
        ref={el => (this.gridDiv = el as HTMLDivElement)}
      ></div>
    );
  }
}
